import { useRef } from "react";
import "./styles.css";
import Child from "./Child";
export default function App() {
  const ref = useRef();
  const hc = () => {
    ref.current.focus();
    ref.current.value = 100;
    ref.current.style.color = "red";
  };
  return (
    <div className="App">
      <button onClick={() => hc()}>btn</button>
      <Child ref={ref} />
    </div>
  );
}
